@mixin flex-column {position: relative;display: -webkit-flex;display: -ms-flexbox;display: -moz-box;display: -webkit-box;display: flex;flex-direction: column;align-items: center;justify-content: center;}
@mixin flex-row {position: relative;display: -webkit-flex;display: -ms-flexbox;display: -moz-box;display: -webkit-box;display: flex;flex-direction: row;align-items: center;justify-content: center;}
@mixin border {border: 1px solid red;};
@mixin absolute {position: absolute; top: 0; left: 0;}
@mixin absolute-btm {position: absolute; bottom: 0; left: 0;}
@mixin btn-eff {transition: all 0.3s; &:hover {transform: scale(1.04, 1.04);} &:active {transform: scale(0.98, 0.98);}}