@font-face {
    font-family: 'Tilda 300';
    src: url('../fonts/Tilda Sans/TildaSans-Light.eot');
    src: local('Tilda Sans Light'), local('TildaSans-Light'),
        url('../fonts/Tilda Sans/TildaSans-Light.eot?#iefix') format('embedded-opentype'),
        url('../fonts/Tilda Sans/TildaSans-Light.woff2') format('woff2'),
        url('../fonts/Tilda Sans/TildaSans-Light.woff') format('woff'),
        url('../fonts/Tilda Sans/TildaSans-Light.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}
